import React, { useCallback, useEffect, useLayoutEffect, useRef, useState } from "react";
import style from "./header.module.scss";

export default function Header({offsets}){
  // ë·°í¬íŠ¸ì— ë”°ë¼ ë²„íŠ¼ ë³´ì´ê¸°
  const [navBtnState,setNavBtnState]= useState(true);
  // ëª¨ë°”ì¼ì—ì„œ ë„¤ë¹„ê²Œì´ì…˜ ë³´ì´ê¸°
  const [navVisibleState,setNavVisible] = useState(true);
  // ë°ìŠ¤í¬íƒ‘ì—ì„œ ëª¨ë°”ì¼ ë·°ë¡œ ë³€ê²½ë  ì‹œ ë„¤ë¹„ ì• ë‹ˆë©”ì´ì…˜ì´ ë³´ì´ëŠ” ê±¸ ë°©ì§€í•˜ê¸° ìœ„í•œ ë²„íŠ¼ í´ë¦­ íšŸìˆ˜       
  const firstClick = useRef(0);
// ì„¹ì…˜ ì´ë™
  const scrolling = useCallback((e)=>{
    window.scrollTo({
      top:offsets[e.target.id],
      behavior: "smooth"
    })
  },[offsets]);
  // ë·°í¬íŠ¸ ë³€ê²½ì‹œ ëª¨ë°”ì¼ ë·° í•´ì œ
  useLayoutEffect(()=>{
    function checkWidth(){
     if(window.innerWidth > 850){
      setNavBtnState(()=>true);
      setNavVisible(()=>true);
      firstClick.current = 0;
     }else{
        setNavVisible(()=>false);
        setNavBtnState(() => false);
      };
    }
   checkWidth();
   window.addEventListener("resize",checkWidth);
   return ()=>{
     window.removeEventListener("resize",checkWidth);
   }
  },[])
// ëª¨ë°”ì¼ ë·°ì—ì„œ ë„¤ë¹„ ì™¸ë¶€ í´ë¦­ì‹œ ë‹«íˆëŠ” ë¡œì§
  useEffect(()=>{
    function closeNavMobile(e){
      if(navVisibleState && !e.target.matches(".header")){
        setNavVisible(()=>false);
      }
    }
    window.addEventListener("click",closeNavMobile);
    // ë°ìŠ¤í¬íƒ‘ ë·°ê°€ ë˜ë©´ ì œê±°
    if(navBtnState){
      window.removeEventListener("click",closeNavMobile);
    }
    return ()=>{
      window.removeEventListener("click",closeNavMobile);
    }
  },[navVisibleState])
// ìµœì´ˆ ë„¤ë¹„ ë²„íŠ¼ í´ë¦­ ê°ì§€, ìµœì´ˆ ë„¤ë¹„ê²Œì´ì…˜ ì„¹ì…˜ì€ display:noneìƒíƒœ, ëˆ„ë¥´ë©´ ë°”ë€œ
  const toggleNav =useCallback((e)=>{
    firstClick.current++;
    setNavVisible(state=>!state);
  },[])

  return(
    <header className={navBtnState?style.off:style.on}>
      <h2 className="a11yHidden">ë©”ì¸ ë„¤ë¹„ê²Œì´ì…˜</h2>
      <button className={navVisibleState ? style.openBtn:style.defaultBtn} onClick={toggleNav}>
        <span className={style.buger}></span>
        <span className={style.buger}></span>
      </button>
      <ul className={navVisibleState ? [style.header,style.openNav].join(" "): firstClick.current ? style.header :style.firstHeader} onClick={scrolling}>
        <li id="main">
            Home
        </li>
        <li id="about">
          About
        </li>
        <li id="skills">
          Skills
        </li>
        <li id="project">
          Project
        </li>
        <li id="contact">
          Contact
        </li>
        <li>
          <a href="https://github.com/winbigcoms?tab=repositories" title="ë°±ìŠ¹ì¼ ê¹ƒí—ˆë¸Œë¡œ ì´ë™!">
            more...?
          </a>
        </li>
      </ul>
    </header>
  )
}